import React from 'react';
import { Link } from "react-router-dom";
import Header from '../header';

function Home(props) {
    let data = JSON.parse(props.project);
    let linkCSS = ({fontWeight : "500"});
    return (
        <>
            <Header number={props.number}></Header>
            <div className="App-container" style={{display: 'flex', alignItems: 'center', justifyContent: 'center', height: 'calc(100vh - 30px)', padding: '15px'}}>
                <table border="1" style={{borderSpacing: '0'}}>
                    <thead>
                        <tr>
                            <th style={{padding: '5px'}}>#</th>
                            <th style={{padding: '5px', minWidth : '110px'}}>Project Name</th>
                            <th style={{padding: '5px'}}>Project Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map((value, key)=>(
                                <tr key={key}>
                                    <td style={{padding: '5px'}}>{value.project_id}</td>
                                    <td style={{padding: '5px'}}>
                                        <Link style={linkCSS} to={`/${value.project_url}`}>{value.project_name}</Link>
                                    </td>
                                    <td style={{padding: '5px'}}>{value.project_description}</td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        </>
    );
}

export default Home;
